<template>
  <div id="app">
    <uziday-navbar :class="[uzidayNavbar ? 'is-activated-navbar-comp' : '']"></uziday-navbar>
    <router-view/>
    <!-- <uziday-footer-ads></uziday-footer-ads> -->
  </div>
</template>
<script>
  import uzidayNavbar from './components/UziDayNavbar';
  import uzidayFooterAds from './components/UziDayFooterAds';
  export default{
    mounted(){
      var height = document.querySelector('.FoundersComp').offsetHeight;
      var positionScroll = document.body.offsetHeight - height;
      if(window.scrollY >= positionScroll)
      {
        this.uzidayNavbar = true;
      }else{
        this.uzidayNavbar = false;
      }
      this.stylingNavbar();
      window.onresize = ()=> this.stylingNavbar();
      document.querySelector('html').style['overflow-x'] = 'hidden';
    },
    created(){
    },
    components:{
      uzidayNavbar,
      uzidayFooterAds
    },
    data:()=>({
      uzidayNavbar: false,
    }),
    methods:{
      stylingNavbar(){
        var height = document.querySelector('.FoundersComp').offsetHeight;
        window.onscroll = ()=>{
          var height = document.querySelector('.FoundersComp').offsetHeight;
          var positionScroll = document.body.offsetHeight - height;
          if(window.scrollY >= positionScroll)
          {
            this.uzidayNavbar = true;
          }else{
            this.uzidayNavbar = false;
          }
        }
      }
    }
  }
</script>
<style>
body{
  font-family: 'Montserrat', sans-serif !important;
}
@media screen and (max-width: 760px)
{
  .title-website{
    padding-top: 10px;
  }
}

