					github: 'https://github.com/mrzamii',
				},
				lauti:{
					facebook: '',
					instagram: 'https://www.instagram.com/lautaumpierrez/',
					github: 'https://github.com/lautaumpierrez',
				}
			},
		}),
		computed:{
			currentFounder(){
